import React from "react";
import ShowMoreText from "react-show-more-text";

import { AppContext, setShowMoreFotterMsg } from "../context";
import { UIScreen } from "../types";

export const AppFooterNote = () => {
    const { state, dispatch } = React.useContext(AppContext);
    const { showMoreFooterMsg } = state;
    const showFooterMsg =
        [UIScreen.INQUIRY_FORM, UIScreen.UPDATE_FORM].includes(
            state.currentPage
        ) && state.employeeInfo?.footerMessage;

    return (
        <div
            className={`ss-eoa-note${showMoreFooterMsg ? " footer-open" : ""}${
                !showFooterMsg ? " hide-footer-note" : ""
            }`}
        >
            <div>
                <ShowMoreText
                    lines={4}
                    anchorClass="show-more-text"
                    onClick={() => {
                        dispatch(
                            setShowMoreFotterMsg(!state.showMoreFooterMsg)
                        );
                    }}
                >
                    {state.employeeInfo?.footerMessage}
                </ShowMoreText>
            </div>
        </div>
    );
};
